.sb-theme-violet {
  // Prism Theme Colors
  --sbt-theme-color-10: var(--sbt-violet-10);
  --sbt-theme-color-20: var(--sbt-violet-20);
  --sbt-theme-color-30: var(--sbt-violet-30);
  --sbt-theme-color-40: var(--sbt-violet-40);
  --sbt-theme-color-50: var(--sbt-violet-50);
  --sbt-theme-color-60: var(--sbt-violet-60);
  --sbt-theme-color-70: var(--sbt-violet-70);
  --sbt-theme-color-80: var(--sbt-violet-80);
  --sbt-theme-color-90: var(--sbt-violet-90);
  --sbt-theme-color-100: var(--sbt-violet-100);

  // Header colors
  --sbt-color-header-dark: var(--sbt-theme-color-60);
  --sbt-color-header-darker: var(--sbt-theme-color-70);
  --sbt-color-header-light: var(--sbt-theme-color-40);
  --sbt-color-header-lighter: var(--sbt-theme-color-30);
}
